<template>
  <div>
    <div class="departmentelrow-div">
      <el-input
        placeholder="请输入科室或者医生名称"
        v-model="input"
        clearable
        class="onedeptinput"
        ><el-button
          icon="el-icon-search"
          style="background:#CBCBCB"
          slot="append"
        ></el-button
      ></el-input>
    </div>
    <div class="div-options" ></div>
    <div>
      <el-cascader-panel
       ref="GocentCascader"
        class="elpanel-indiv"
        :options="options"
         :props="optionProps"
         @change="handlepanelChange"
      ></el-cascader-panel>
    </div>
  </div>
</template>
<script>
import * as commonApi from "../../api/commonfetch";
export default {
  name: "Category",
  data() {
    return {
      options:[],
      optionProps:{
		  value: 'DeptCode',
		  label: 'DeptName',
		  children: 'ChilDeptInfos'
	},
      input: "",
      onedeptinfo: [],
      twodeptinfo: []
    };
  },
  methods: {
      handlepanelChange(value) {
        //跳转页面
        console.log(value)
          console.log(value[0])
         this.$router.push({
          name: 'departlist',
          query: {
            deptone: value[0],
            depttwo:value[1]
          }
        })
      },
    getonedept() {
       const paramsr = {
        hospitalcode: "1"
        };

      commonApi
        .AllDeptInfo(paramsr)
        .then(res => {   
       
          if (res.data.success === true) {
               console.log(res.data);
               this.options=res.data.data.DeptInfos
              
          } else {
      
          }
        })
        .catch(err => {
          console.log(err);
        });
    }
  },
  mounted: function() {
    this.getonedept();
  }
};
</script>
<style scoped>
@import "../../assets/css/gshawn.css";
.div-options {
  margin-top: 5%;
  height: 100%;
  width: 100%;
  position: absolute;
}
.elpanel-indiv {
   height: 100%;
  width: 100%;
  position: absolute;
}

</style>



